import React , {useState} from 'react';

export const AddTodo = (props) => {
  const [title  , setTitle] = useState("");
  const [desc  , setDesc] = useState("");

  const submit = (e) =>{
    e.preventDefault();
    if (!title || !desc){
      alert('Title or Description cannot be blank')
    }
    else{
     props.addTodo(title, desc)
     setTitle("")
     setDesc("")
    }
  }
  return (
    <div className="container my-3 w-50">
      <h3 className="text-center">Add Todo</h3> <hr/>
      <form>
        <div className="mb-3">
          <label htmlFor="title" className="form-label">Todo Title</label>
          <input type="text" value={title} onChange={(e) =>{setTitle(e.target.value)}} id="title" className="form-control border border-primary" id="exampleInputEmail1" aria-describedby="emailHelp" />
        </div>
        <div className="mb-3">
          <label htmlFor="desc" className="form-label">Todo description</label>
          <input  type="text" value={desc} onChange={(e) =>{setDesc(e.target.value)}}  className="form-control border border-primary" />
        </div>
        <button type="submit" className="btn btn-success btn-sm" onClick ={submit} >ADD</button> <hr/>
      </form>
    </div>
  )
}
