import Header from "./components/header";
import { Todos } from "./components/todos";
import { Footer } from "./components/footer";
import { AddTodo } from "./components/AddTodo";
import React, { useState, useEffect } from 'react';
import { About } from "./components/about";
import './App.css'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

function App() {
  let initTodo;
  if (localStorage.getItem('todoitems') === null) {
    initTodo = [];
  }
  else {
    initTodo = JSON.parse(localStorage.getItem('todoitems'));
  }
  const onDelete = (todo) => {
    console.log('i am onDelete of todo', todo);
    // let index = todoitems.indexOf(todo);  
    // todoitems.splice(index , 1);                             ----> this method of delete in react will not work
    setTodos(todoitems.filter((e) => {
      return e !== todo;
    }))
  }

  const addTodo = (title, desc) => {
    console.log('adding todo...', title, desc)
    let sno;
    if (todoitems.length == 0) {
      sno = 1;
    }
    else {
      sno = todoitems[todoitems.length - 1].sno + 1;
    }
    const myTodo = {
      sno: sno,
      title: title,
      desc: desc,
    }
    setTodos([...todoitems, myTodo])
    console.log(myTodo);
  }
  const [todoitems, setTodos] = useState(initTodo);
  useEffect(() => {
    localStorage.setItem('todoitems', JSON.stringify(todoitems));
  }, [todoitems])
  return (
    <>
      <Router >
        <Header title="My Todos List" searchBar={false} />

        <Switch>
          <Route path="/" render={() => { 
            return (
              <>
                <AddTodo addTodo={addTodo} />
                <Todos todos={todoitems} onDelete={onDelete} />
              </>
            )
          }}>
          </Route>
          <Route  exact path="/about">
            <About />
          </Route>
        </Switch>

        <Footer />
      </Router>
    </>
  );
}

export default App;
